.calculator{
    max-width: 1000px;
    height: 740px;
    box-sizing: border-box;
    margin: auto;
    background-color: #000000;
    opacity: 0.8;
    padding: 25px;
    margin-top: 25px;
    border: 2px solid #cc0000;
    border-radius: 2%;
}

.calc-div{
    box-sizing: border-box;
    display: inline-block;
    margin: 0 auto;
    height: 360px;
    width: 470px;
    z-index: 1000;
}

#left-top{
    float: left;
    padding-top: 10px;
}

#left{
    float: left;
}

#right{
    float: right;
}

@media screen and (max-width: 1100px) {
    .calculator{
        max-width: 500px;
        min-height: 1510px;
        border-radius: 0;
    }
    .calc-div{
        max-width: 100%;
    }
}

ul{
    text-align: left;
}

table, th, td{
    width: 100%;
}

th, td{
    background-color: #031216;
}

.col1{
    width: 50%;
}

.col23{
    width: 25%;
}

.greenBox{
    width: 25%;
    background-color: green;

}

.redBox{
    width: 25%;
    background-color: red;

}