.intro{
    max-width: 700px;
    margin: auto;
}
h1, h2, p, ul{
    color: #ffffff;
    font-family: 'Bree Serif', serif;
}

p, ul{
    font-size: 18px;
}

.red{
    color: red;
    display: inline;
    font-size: 22px;
}

.green{
    color: green;
    display: inline;
    font-size: 22px;
}

.white{
    color: #ffffff;
    display: inline;
    font-size: 22px;
}

button{
    background-color: #000000;
    font-size: 22px;
    color: #ffffff;
    font-family: 'Bree Serif', serif;
    border: 2px solid #cc0000;
    border-radius: 2%;
    padding: 10px;
}

button:focus{
    outline: 0;
}

input{
    width: 60px;
    font-size: 18px;
    font-family: 'Bree Serif', serif;
    text-align: center;
    background-color: #ffffff;
}